import React from "react";

import { Link } from "react-router-dom";

import "./style.css";

export default class ButtonRouter extends React.Component {
  render() {
    const { attribute, bordered, success } = this.props;
    let componentClass = 'buttonLink ';

    if (bordered) componentClass += 'buttonLink--bordered ';
    if (success) componentClass += 'buttonLink--success ';

    return (
      <Link to={attribute.href} className={componentClass}>
        {attribute.value}
      </Link>
    );
  }
}
