import React from "react";

// constants
import { UP, DOWN, LEFT, RIGHT } from "components/arrows/constants";
import { WALL, BOX, PLAYER, GOAL, BOXONGOAL } from "data/constants";

// components
import Map from "components/map";
import Controller from "components/controller";
import Button from "components/button";
import ButtonRouter from "components/buttonrouter";

// data
import levels from "data/levels.json";

export default class Game extends React.Component {
  state = {
    player: {
      row: 0,
      column: 0,
      direction: DOWN
    },
    map: {
      walls: [],
      boxes: [],
      goals: [],
      row: 0,
      column: 0
    },
    isDone: false,
    memory: []
  };

  componentDidMount = () => {
    document.title = 'Sokoban - Level ' +  this.props.match.params.id;
    this.restartLevel();
    document.addEventListener("keydown", this.handleKeyPress, false);    
  }

  componentDidUpdate(prevProps) {
    if (this.props.match.params.id !== prevProps.match.params.id) {
      this.restartLevel();
      this.setState({
        isDone: false
      })
    }
   }

  componentWillUnmount(){
    document.removeEventListener("keydown", this.handleKeyPress, false);
  }

  restartLevel = () => {
    let player = {};
    let walls = [];
    let boxes = [];
    let goals = [];
    let mapCounter = {row: 0, column: 0};
    const level = Number(this.props.match.params.id) - 1;
    const map = levels.classic[level];
    const mapSplit = map.split("/");

    mapSplit.map((mapRow, rowCounter) => {
      mapRow.split("").map((mapCell, colCounter) => {
        switch (mapCell) {
          case PLAYER:
            player.row = rowCounter;
            player.column = colCounter;
            player.direction = DOWN;
            break;
          case BOXONGOAL:
            boxes = [...boxes, { row: rowCounter, column: colCounter }];
            goals = [...goals, { row: rowCounter, column: colCounter }];
            break;            
          case BOX:
            boxes = [...boxes, { row: rowCounter, column: colCounter }];
            break;
          case GOAL:
            goals = [...goals, { row: rowCounter, column: colCounter }];
            break;
          case WALL: 
            walls = [...walls, { row: rowCounter, column: colCounter }];
            break;
          default:
            break;
        }

        mapCounter.column = colCounter > mapCounter.column ? colCounter : mapCounter.column;
        return null;
      });

      mapCounter.row = rowCounter > mapCounter.row ? rowCounter : mapCounter.row;      
      return null;
    });

    mapCounter.row++;
    mapCounter.column++;
    this.setState({
      player,
      map: {
        ...mapCounter,
        walls,
        boxes,
        goals,
      },
      level,
      memory: [],
      isDone: false
    });
  };

  handleButton = direction => () => {
    let newPlayerPosition = { row: this.state.player.row, column: this.state.player.column };
    let walls = [...this.state.map.walls];
    let boxes = [...this.state.map.boxes];
    let goals = [...this.state.map.goals];
    let tileInFront = { ...newPlayerPosition };
    let memory = [...this.state.memory];
    let boxesInGoal = 0;
    const objectInArray = (object, array) => array.find(x => x.row === object.row && x.column === object.column);

    // getting coordinates
    switch (direction) {
      case UP:
        newPlayerPosition.row--;
        tileInFront.row -= 2;
        break;
      case DOWN:
        newPlayerPosition.row++;
        tileInFront.row += 2;
        break;
      case LEFT:
        newPlayerPosition.column--;
        tileInFront.column -= 2;
        break;
      case RIGHT:
        newPlayerPosition.column++;
        tileInFront.column += 2;
        break;
      default:
        break;
    }

    if ( 
      objectInArray(newPlayerPosition, boxes) &&
      !objectInArray(tileInFront, boxes) &&
      !objectInArray(tileInFront, walls)
    ) { // pushing a box without anything behind it
      boxes[boxes.findIndex(x => x.row === newPlayerPosition.row && x.column === newPlayerPosition.column)] = {...tileInFront};

      if (memory.length === 10) memory.shift();

      this.setState({
        player: { ...newPlayerPosition, direction },
        memory: [
          ...memory,
          { player: this.state.player, boxes: this.state.map.boxes }
        ],
        map: {
          ...this.state.map,
          boxes
        }
      });
    } else if ( // move player
      !objectInArray(newPlayerPosition, walls) &&
      !objectInArray(newPlayerPosition, boxes)
    ) {
      this.setState({
        player: { ...newPlayerPosition, direction }
      });
    }
    else { // change player's direction ONLY
      this.setState({
        player: { ...this.state.player, direction }
      });
    }

    // check if level is complete
    boxes.map(box => {
      if (objectInArray(box, goals)) boxesInGoal++;
      if (boxesInGoal === goals.length) {
        this.props.addToCompleteLevels(Number(this.props.match.params.id) - 1)();
        this.setState({
          isDone: true
        });
      }

      return null;
    });
  };

  undoMove = () => {
    if (this.state.memory.length) {
      const memory = this.state.memory.pop();

      this.setState({
        player: memory.player,
        map: {
          ...this.state.map,
          boxes: memory.boxes
        }
      });
    }
  };

  handleKeyPress = (event) => {
    if (this.state.isDone === false) {
      switch(event.key) {
        case 'ArrowUp':
        case 'w':
          this.handleButton(UP)();
          break;
        case 'ArrowDown':
        case 's':
          this.handleButton(DOWN)();
          break;
        case 'ArrowLeft':
        case 'a':
          this.handleButton(LEFT)();
          break;
        case 'ArrowRight':
        case 'd':
          this.handleButton(RIGHT)();
          break;
        default:
          break;
      }
    }
  }

  render() {
    const { isDone, memory, player, map } = this.state;
    const id = Number(this.props.match.params.id);
    const options = [
      <Button value={<span className="fa fa-refresh"></span>} handleClick={this.restartLevel}/>,
      <ButtonRouter attribute={{href: '/', value: <span className="fa fa-home"></span>}} bordered/>,
      isDone ? 
        <ButtonRouter attribute={{href: `/game/${id + 1}`, value: <span className="fa fa-level-up"></span>}} bordered/> : 
        <Button value={<React.Fragment><span className="fa fa-history"></span> {memory.length}</React.Fragment>} handleClick={this.undoMove} />
    ]

    return (
      <React.Fragment>
        <Map player={player} map={map} />
        <Controller options={ options } isDone={ isDone } handleButton={this.handleButton}/>
      </React.Fragment>
    );
  }
}
