import React from "react";

import "./style.css";

import Tile from "../tile";

export default class Map extends React.Component {
  render() {
    const { player, map } = this.props;
    const { walls, boxes, goals } = map;
    const objectInArray = (object, array) => array.find(x => x.row === object.row && x.column === object.column);
    const sameObject = (obj1, obj2) => obj1.row === obj2.row && obj1.column === obj2.column;
    let mapLayout = [];

    for (let row = 0; row < map.row; row++) {
      let mapRow;
      let mapRowArray = [];

      for (let column = 0; column < map.column; column++) {
        const cell = {row, column};
        let mapCell = <Tile key={`${row} ${column}`} value={
          sameObject(player, cell) ? `player${player.direction}` :
          objectInArray(cell, boxes) && objectInArray(cell, goals) ? 'boxOnGoal' :
          objectInArray(cell, boxes) ? 'box' :
          objectInArray(cell, goals) ? 'goal' :
          objectInArray(cell, walls) ? 'wall' : ''
        } />

        mapRowArray = [...mapRowArray, mapCell];
        mapRow = <tr key={row}>{mapRowArray}</tr>
      }

      mapLayout = [...mapLayout, mapRow]
    }

    return (
      <table className="map">
        <tbody>{mapLayout}</tbody>
      </table>
    );
  }
}
