import React from "react";

import "./style.css";

import Button from "../button";

// constants
import { UP, DOWN, LEFT, RIGHT } from "./constants";

export default class Arrows extends React.Component {
  render() {
    const { handleButton } = this.props;

    return (
      <div className="controls">
        <div className="controls__top">
          <Button value={<span className="fa fa-angle-up"></span>} handleClick={handleButton(UP)}/>
        </div>
        <div className="controls__middle">
          <Button value={<span className="fa fa-angle-left"></span>} handleClick={handleButton(LEFT)}/>
          <Button value={<span className="fa fa-angle-right"></span>} handleClick={handleButton(RIGHT)}/>
        </div>
        <div className="controls__bottom">
          <Button value={<span className="fa fa-angle-down"></span>} handleClick={handleButton(DOWN)}/>
        </div>
      </div>
    );
  }
}
