import React from "react";

import ButtonRouter from "../../components/buttonrouter";

export default class Page404 extends React.Component {
  componentDidMount() {
    document.title = 'Sokoban - 404';
  }

  render() {
    const attribute = {
      href: '/',
      value: 'Back to Home'
    }
    
    return (
      <React.Fragment>
          <h1 className="content__title">Page 404</h1>
          <p className="content__description">Sorry. The page does not exist</p>
        <ButtonRouter attribute={attribute} bordered>Back to Home</ButtonRouter>
      </React.Fragment>
    );
  }
}
