import React from "react";

import "./style.css";

import List from "../../components/list";
import Link from "../../components/link";
import ButtonRouter from "../../components/buttonrouter";

import levels from "../../data/levels.json";

export default class Home extends React.Component {
  componentDidMount() {
    document.title = 'Sokoban - Home';
  }

  render() {
    const length = 5;
    const credits = [
      <React.Fragment>
        <Link attribute={{href: "http://www.kenney.nl/", value: 'Kenney'}}/> for the assets!
      </React.Fragment>,
      <React.Fragment>
        <Link attribute={{href: "http://sokoban-jd.blogspot.com/", value: "Thinking Rabbit"}} /> for the levels!
      </React.Fragment>
    ];
    let groupArray = (arr, length) => {
      let newArray = [];
      let slicedArray = arr.slice(0, length);
      let theRestOfArray = arr.slice(length);
    
      do {
        newArray = [...newArray, slicedArray];
        slicedArray = theRestOfArray.slice(0,length);
        theRestOfArray = theRestOfArray.slice(length);
        } while(slicedArray.length > 0);
      
      return newArray;
    }
    const { completeLevels } = this.props;

    return (
      <React.Fragment>
        <h1 className="content__title">SOKOBAN</h1>
        <div className="content__description">
          Credits to:
          <List items={credits} vertical></List>
          <p>
            Push all the boxes until all of them are above the green gem<br />
            Use the buttons on screen (or WASD/arrow keys) to move the character
          </p>
        </div>

        {groupArray(levels.classic, length).map((levelGroup, groupIndex) => {
          const segment = levelGroup.map((level, index) => {
            const levelNumber = groupIndex * length + index;
            const attribute = {
              href: `/game/${levelNumber + 1}`,
              value: levelNumber + 1
            };

            if (completeLevels.includes(levelNumber)) return <ButtonRouter key={index} attribute={attribute} success />;
            else return <ButtonRouter key={index} attribute={attribute} bordered />
          });

          return <List key={groupIndex} items={segment}/>
        })}
      </React.Fragment>
    );
  }
}
