import React from "react";

import "./style.css";

export default class ButtonLink extends React.Component {
  render() {
    const { attribute, bordered, success } = this.props;
    let componentClass = 'buttonLink ';

    if (bordered) componentClass += 'buttonLink--bordered ';
    if (success) componentClass += 'buttonLink--success ';

    return (
      <a href={attribute.href} target="_blank" rel="noopener noreferrer" className={componentClass}>
        {attribute.value}
      </a>
    );
  }
}
